<template>
    <!-- Two-way Data-Binding -->
  <quill-editor
    ref="myQuillEditor"
    v-model="content"
    style="height: 500px;"
    :options="editorOption"
  />
  
</template>
<script>
import { quillEditor,Quill} from 'vue-quill-editor'
import ImageResize from 'quill-image-resize-module'
import 'quill/dist/quill.snow.css' // for snow theme

Quill.register('modules/imageResize', ImageResize)
//const ImageResize = require('quill-image-resize-module').default
export default {
    name:"ArticleEditor",
    components:{
        quillEditor
    },
    data(){
      return{
        content:"",
         titleConfig :{
            'ql-bold': '',
            'ql-color': '',
            'ql-font': '',
            'ql-code': '',
            'ql-italic': '',
            'ql-link': '',
            'ql-background': '',
            'ql-size': '',
            'ql-strike': '',
            'ql-script': '/',
            'ql-underline': '',
            'ql-blockquote': '',
            'ql-header': '',
            'ql-indent': '',
            'ql-list': '',
            'ql-align': '',
            'ql-direction': '',
            'ql-code-block': '',
            'ql-formula': '',
            'ql-image': '',
            'ql-video': '',
            'ql-clean': ''
          },
         editorOption: {
          //  
          modules: {
            imageResize: {//
              displayStyles: {
                backgroundColor: "black",
                border: "none",
                color: "white",
              },
              modules: ["Resize", "DisplaySize", "Toolbar"],
            },
            toolbar: {
              container: [
                ["bold", "italic", "underline", "strike"], //     -----['bold', 'italic', 'underline', 'strike']
                ["blockquote", "code-block"], //   -----['blockquote', 'code-block']
                [{ header: 1 }, { header: 2 }], // 12 -----[{ header: 1 }, { header: 2 }]
                [{ list: "ordered" }, { list: "bullet" }], // -----[{ list: 'ordered' }, { list: 'bullet' }]
                [{ script: "sub" }, { script: "super" }], // /-----[{ script: 'sub' }, { script: 'super' }]
                [{ indent: "-1" }, { indent: "+1" }], // -----[{ indent: '-1' }, { indent: '+1' }]
                [{ direction: "rtl" }], // -----[{'direction': 'rtl'}]
                [{ size: ["small", false, "large", "huge"] }], // -----[{ size: ['small', false, 'large', 'huge'] }]
                [{ header: [1, 2, 3, 4, 5, 6, false] }], // -----[{ header: [1, 2, 3, 4, 5, 6, false] }]
                [{ color: [] }, { background: [] }], // -----[{ color: [] }, { background: [] }]
                [{ font: [] }], // -----[{ font: [] }]
                [{ align: [] }], // -----[{ align: [] }]
                ["clean"], // -----['clean']
                ["image", "video"] // -----['link', 'image', 'video']
              ],  // 
             
            },
          },
          //
          placeholder: ""

        }

        }
      },
      mounted(){
        this.addQuillTitle()
      },
      methods:{
        addQuillTitle:function() {
          const oToolBar = document.querySelector('.ql-toolbar'),
          aButton = oToolBar.querySelectorAll('button'),
          aSelect = oToolBar.querySelectorAll('select')
          aButton.forEach(item=>{
            if (item.className === 'ql-script') {
              item.value === 'sub' ? item.title = '' : item.title = ''
            } else if (item.className === 'ql-indent') {
              item.value === '+1' ? item.title = '' : item.title = ''
            } else {
              item.title = this.titleConfig[item.classList[0]]
            }
          })
          aSelect.forEach(item=>{
            item.parentNode.title = this.titleConfig[item.classList[0]]
          })
        }
      }
}
</script>